        </small>
        <div class="control">
          <input @keydown="addTodo($event)" type="text" placeholder="Add ToDo here">
          <span class="focus"></span>
        </div>
      </div>
      <div class="todos--list" v-if="todos.length > 0">
        <div class="open">
          <h3>Open ToDo's:</h3>
          <transition-group name="fade" mode="out-in">
            <div class="todo" v-for="todo in openTodos" :key="todo.id">
              <h4>
                <span @click="openTodo(todo.id)">{{ todo.title }}</span>
              </h4>
              <button class="check-todo" @click="checkTodo(todo.id)">
                <font-awesome-icon icon="check-circle"></font-awesome-icon>
              </button>
              <button class="del-todo" @click="delTodo(todo.id)">
                <font-awesome-icon icon="trash-alt"></font-awesome-icon>
              </button>
            </div>
          </transition-group>
        </div>
        <div class="done">
          <h3>Finished ToDo's:</h3>
          <transition-group name="fade" mode="out-in">
            <div class="todo" v-for="todo in doneTodos" :key="todo.id">
              <h4>
                <span @click="openTodo(todo.id)">{{ todo.title }}</span>
              </h4>
              <button class="del-todo" @click="delTodo(todo.id)">
