            <div class="status">
              <div class="done" v-if="selectedTodo[0].done === true">
                <p class="info">Status:</p>
                <p>
                  This ToDo is already
                  <i class="color--pri">done!</i>
                </p>
                <i>Good job!</i>
              </div>
              <div class="open" v-else>
                <p class="info">Status:</p>
                <p>
                  This ToDo is
                  <i class="color--sec">not done</i> yet!
                </p>
                <p>
                  You can do it, I believe in you
                  <strong class="color--pri">!</strong>
                </p>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </section>
  </div>
</template>
