<script>
export default {
  name: "home",
  data() {
    return {
      todos: {},
      selectedTodo: {}
    };
  },
  created() {
    this.selectedTodo = {};
    axios
      .get("/todos")
      .then(response => ((this.todos = response.data), console.log(response)));
  },
  methods: {
    addTodo(ev) {
      if (ev.keyCode === 13) {
        const value = ev.target.value;
        console.log("add", value);
        axios
          .post("/todos", {
            title: value
          })
          .then(
            response => (
              this.$set(this.todos, this.todos.length, response.data),
              console.log(response.data)
            )
          );
        ev.target.value = "";
      }
    },
    checkTodo(id) {
      console.log(id);
      axios
        .put(`/todos/${id}/done`)
        .then(response => (_.find(this.todos, el => el.id === id).done = true));
    },
    delTodo(id) {
      console.log(
        "id: ",
        id,
        "key: ",
        _.findKey(this.todos, el => el.id === id)
      );
      console.log(this.todos);
      axios.delete(`/todos/${id}`);
      this.$delete(this.todos, _.findKey(this.todos, el => el.id === id));
    },
    openTodo(id) {
      console.log(_.find(this.todos, el => el.id === id));
      this.$set(this.selectedTodo, 0, _.find(this.todos, el => el.id === id));
      console.log(this.selectedTodo);
    },
    closeTodo() {
      this.selectedTodo = {};
    }
  },
  computed: {
    openTodos() {
      return _.filter(this.todos, (val, key) => val.done === false);
    },
    doneTodos() {
      return _.filter(this.todos, (val, key) => val.done === true);
    },
    selectedTodoExists() {
      return Object.keys(this.selectedTodo).length > 0;
    }
  }
};
</script>
